        <v-container v-if="!hasReplications() && selectedTab==1 && !isReplication()" 
          style="color: #005665" class="mb-5 text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption">
          <v-img class="mb-2" :src="require('@/assets/arabica-18@2x.png')" contain aspect-ratio="2.5"></v-img>
          <p class="font-weight-bold color-primary" v-if="finding.UserId==userId">Replicate your inquiry to validate it</p>
          <p class="font-weight-bold" v-else>Be the first to replicate this!</p>
        </v-container>

        <v-container v-if="!hasRemixes() && selectedTab==2 && !isReplication()" 
          style="color: #005665" class="mb-5 text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption">
          <v-img class="mb-2" :src="require('@/assets/arabica-upgrade@2x.png')" contain aspect-ratio="2.5"></v-img>
          <p class="font-weight-bold">No remixes yet</p>
        </v-container>
        
        <v-btn v-if="isOriginal() || isRemix()" 
          class="rounded-lg mb-5 text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption text-capitalize" 
          large color="primary" block @click="replicateOrRemix()"
        >{{selectedTab==1? 'Replicate': 'Remix'}}
        </v-btn>

        <v-card color="primaryBackground" flat class="pb-3 mb-5 rounded-lg invite-others-section"
          v-if="!hasReplications() && !hasRemixes() && isOwnFinding()"
        > 
          <v-container class="font-weight-bold text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption">
            Invite others to view your inquiry
          </v-container>
          <v-container class="pa-0">
            <v-btn large class="rounded-lg mx-1 mb-2 pa-2 font-weight-bold text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption text-capitalize" tile outlined color="primary" style="background-color: white;"  @click="copyLink()">
              <v-avatar link size="32" class="mx-1">
                <v-img class="d-inline-flex" :src="require('@/assets/insert-link-24-px@2x.png')"></v-img>
              </v-avatar> {{copyLinkText}}
            </v-btn>
            <v-btn large class="rounded-lg mx-1 mb-2 pa-2 font-weight-bold text-xl-h5 text-lg-h6 text-md-body-1 text-sm-body-2 text-xs-caption text-capitalize" tile outlined color="primary" style="background-color: white;" @click="inviteOthers()">
              <v-avatar link size="32" class="mx-1">
                <v-img class="d-inline-flex" :src="require('@/assets/mail-24-px@2x.png')"></v-img>
              </v-avatar> Send Invite
            </v-btn> 
          </v-container>
        </v-card>

        <v-row
          align="start" justify="start" class="pa-3 mt-0 mb-2" v-for="inquiry in inquiryList" v-bind:key="inquiry.id"
        >
          <v-card class="rounded-lg" link style="text-align: left;" @click="loadInquiry(inquiry.Id)">
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(inquiry)"
              lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg"
            >
            </v-img>
            <v-chip
              class="ma-1 mb-0"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left style="margin-right: 5px;">
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              {{inquiry.UserNickname}}
            </v-chip>
            <v-card-title class="py-1 mt-0 mb-1" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{inquiry.Name}}</v-card-title>
          </v-card>
        </v-row>
      </v-container>
      </v-col>     
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
  export default {
    data() {
      return {
        username:"",
        finding:{Name:"", Description:"", Dataset:[{Media:""}], Type:1, Base:null, Status:2},
        parentFinding:{Name:"", Description:"", Dataset:[{Media:""}], Type:0, Base:"_"},
        comments: [],
        replications: [],
        similarResults: 0,
        differentResults: 0,
        notsureResults: 0,
        variations: [],
        selectedTab: 1,
        isCommentInputEnabled: false,
        commentValue: "",
        commentType: 0,
        selectedCommentType: 0,
        datasetIndex: 0,
        userId: "",
        showPublishMenu: false,
        selectedComment: null,
        originalComment: {value:"", type:0},
        commentToDelete: "",
        interceptors: 0,
        showLoading: false,
        linkCopied:false,
        copyLinkText: "Copy Link",
        items: [
          { title: 'Unpublish', icon: require('@/assets/visibility-off-24-px@2x.png')},
          { title: 'Copy Link', icon: require('@/assets/insert-link-24-px@2x.png') },
          { title: 'Invite to view', icon: require('@/assets/mail-24-px@2x.png') },
          { title: 'Delete', icon: require('@/assets/baseline_delete_forever_black_24dp.png') },
        ],
        inquiryList: [],
        similarityIndicators: [
          {name: 'Similar', icon: '😄'},
          {name: 'Different', icon: '🤔'},
          {name: 'Unknown', icon: '🙃'},
        ],
        mainButton: {
          name: 'Replicate',
        },
        slides: [
          'https://kiwrious.org/Storage/System/default-placeholder.jpg'
        ],
        commentTypes: [
          {type: 0, name:'Compliment', icon: require('@/assets/add-24-px@2x.png')},
          {type: 1, name:'Compliment', icon: require('@/assets/favorite-24-px@2x.png')},
          {type: 2, name:'Question', icon: require('@/assets/help-g-24-px@2x.png')},
          {type: 3, name:'Suggestion', icon: require('@/assets/wb-incandescent-24-px@2x.png')}
        ],
        tabs: [
          {index:1, name: 'Replications', data: this.replications},
          {index:2, name: 'Remixes', data: this.variations},
        ]
      }
    },
    props: ['id'],
    // beforeCreate(){
    //   this.$loading.show({delay:0, background: '#FFFFFF'});  
    // },
    created() {
      console.log(process.env.VUE_APP_URL);
      var findingId = sessionStorage.findingId;
      if(this.id){
        findingId = this.id;
      }
      this.username = sessionStorage.name;
      this.userId = sessionStorage.uid;

      this.interceptors++;
      api.get('/finding/'+findingId).then((response)=> {
        this.interceptors--;
        this.$loading.hide();
        this.finding = response.data;
        this.processDataImages();
        if(this.finding.Base){
          api.get('/finding/'+this.finding.Base).then((response)=> {
            this.parentFinding = response.data;
            console.log(this.parentFinding);
          });
        }
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });
      this.interceptors++;
      api.get('/comment/finding/'+findingId).then((response) => {
        this.interceptors--;
        this.comments = response.data;
      });

      this.interceptors++;
      api.get('/finding/replication/'+findingId).then((response)=>{
        this.interceptors--;
        this.replications = response.data;
        this.inquiryList = this.replications;
        this.calculateSimilarities();
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });

      this.interceptors++;
      api.get('/finding/variation/'+findingId).then((response)=>{
        this.interceptors--;
        this.variations = response.data;
      }).catch((error)=>{
        this.interceptors--; 
        alert(error.message);
      });

    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      enableCommentInput(type){
        if(this.selectedComment){
          return;
        }
        this.isCommentInputEnabled = true;
        this.selectedCommentType = type;
        this.commentType = type;
      },
      postComment(){
        const url = '/comment/';
        const comment = {
          userId : sessionStorage.uid,
          findingId: sessionStorage.findingId,
          value: this.commentValue,
          type: this.commentType
        }
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(comment),config).then((response)=>{
          var savedComment = response.data;
          this.isCommentInputEnabled = false;
          this.commentType = 0;
          this.commentValue = "";
          this.comments.splice(0,0,response.data);
        });
      },
      getMedia(finding){
        if(finding.Dataset.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(!finding.Dataset[this.datasetIndex]){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset[this.datasetIndex].Media == null || finding.Dataset[this.datasetIndex].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[this.datasetIndex].Media+'.jpg';
      },
      replicateOrRemix(){
        var newFinding = this.finding;
        if(this.selectedTab==1){
          newFinding.Type = 2;
        } else {
          newFinding.Type = 3;
          newFinding.Dataset.Condition = "";
        }
        newFinding.Dataset.Uv = 0;
        newFinding.Dataset.Lux = 0;
        newFinding.Dataset.Media = "";
        newFinding.Base = this.finding.Id;
        newFinding.Id = null;
        newFinding.UserId = sessionStorage.uid;
        this.$store.dispatch('setFindingData', newFinding);
        this.$router.push({path: '/find_ar'});
      },
      calculateSimilarities(){
        this.similarResults = 0;
        this.differentResults = 0;
        this.notsureResults = 0;
        this.replications.forEach((r)=>{
          if(r.Similarity == 1){
            this.similarResults++;
          } else if(r.Similarity == 2){
            this.differentResults++;
          } else {
            this.notsureResults++;
          }
        })
      },
      loadFinding(findingId){
        console.log("load finding...");
        this.$store.dispatch('setFindingIdData', findingId);
        window.location.reload(); //find the vue method to reload component
      },
      loadInquiry(findingId){
        console.log("load finding...");
        this.$store.dispatch('setFindingIdData', findingId);
        window.location.reload(); //find the vue method to reload component
      },
      setDatasetIndex(value){
        if(this.finding.Dataset.length==0){return;}
        if(this.datasetIndex+value<0){
          return;
        }
        if(this.datasetIndex+value == this.finding.Dataset.length){
          return;
        }
        this.datasetIndex += value;
        this.reload();
      },
      reload() {
        this.$forceUpdate();
      },
      jump(url) {
        this.$router.push({path: '/find_ar'})
      },
      unpublish(){
        this.showPublishMenu = false;
        const url = '/finding/' + this.finding.Id;
        this.finding.Status = 1;
        console.log(this.finding);
        api.put(url, this.finding).then((response)=>{
          this.$router.push({path: '/home/myinquiries'});
        });
      },
      copyLink(){
        this.showPublishMenu = false;
        var findingLink = "https://learn.kiwrious.com/#/login/"+this.finding.Id;
        navigator.clipboard.writeText(findingLink);
        this.linkCopied = true;
        this.copyLinkText = "Copied";
        // window.setTimeout(()=>{this.linkCopied=false;}, 3000);
      },
      inviteOthers(){
        this.showPublishMenu = false;
        var subject = this.username+"'s Kiwrious Inquiry";
        var encodedSubject = encodeURIComponent(subject);
        var findingLink = "https://learn.kiwrious.com/#/login/"+this.finding.Id;
        var body = "Check out my new Kiwrious Inquiry: " + findingLink + " \nI'm inviting you to comment on, replicate or remix it!";
        var encodedBody = encodeURIComponent(body);
        document.location.href = "mailto:?subject="+encodedSubject+"&body="+encodedBody;
      },
      deleteFinding(){
        this.$dialog({content: 'Permanently delete this inquiry? You can\'t undo this',
          btns: [{label: 'Cancel',color: '#444',ghost: true}, {label: 'Yes',color: 'gray',callback: this.onConfirmDeleteFinding}]
        });
      },
      onConfirmDeleteFinding(){
        this.showPublishMenu = false;
        const url = '/finding/' + this.finding.Id;
        api.delete(url).then((response)=>{
          this.$router.push({path: '/home/myinquiries'});
        });
      },
      deleteComment(commentId){
        this.commentToDelete = commentId;
        this.$dialog({content: 'Delete this comment?',
          btns: [{label: 'Cancel',color: '#444',ghost: true}, {label: 'Yes',color: 'gray',callback: this.onConfirmDeleteComment}]
        });
      },
      onConfirmDeleteComment(){
        const url = '/comment/' + this.commentToDelete;
        var deletedIndex = this.comments.indexOf(this.comments.find(c=> c.Id==this.commentToDelete));
        this.comments.splice(deletedIndex, 1);
        api.delete(url).then((response)=>{
         console.log(response);
        });
      },
      editComment(comment){
        if(this.isCommentInputEnabled){return;}
        this.selectedComment = comment;
        this.originalComment.value = comment.Value;
        this.originalComment.type = comment.Type;
      },
      revertComment(comment){
        comment.Value = this.originalComment.value;
        comment.Type=  this.originalComment.type;
        // this.selectedComment = this.originalComment;
        this.selectedComment = null;
      },
      saveComment(editedComment){
        this.selectedComment = null;
        const url = '/comment/' + editedComment.Id;
        var comment = {id:editedComment.Id, value:editedComment.Value, type:editedComment.Type}
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.put(url, qs.stringify(comment),config).then((response)=>{
          
        });
      },
      isOriginal(){
        return this.finding.Type==1;
      },
      isRemix(){
        return this.finding.Type==3;
      },
      isReplication(){
        return this.finding.Type==2;
      },
      hasReplications(){
        return this.replications.length>0;
      },
      hasRemixes(){
        return this.variations.length>0;
      },
      isOwnFinding(){
        return this.finding.UserId==this.userId;
      },
      openDialog(method){
          this.$dialog({title: 'Confirm deletion', content: 'Do you want to delete this?',
          btns: [{label: 'Yes',color: '#09f',callback: method},{label: 'Cancel',color: '#444',ghost: true}]
      });
      },
      getSceneNotes(){
        debugger;
        if(this.finding && this.finding.Dataset && this.finding.Dataset[this.datasetIndex]){
          debugger
          return this.finding.Dataset[this.datasetIndex].Notes;
        }
        return "";
      },

      selectPublishTask(taskName){
        switch(taskName){
          case 'Unpublish': this.unpublish();break;
          case 'Copy Link': this.copyLink();break;
          case 'Invite to view': this.inviteOthers();break;
          case 'Delete': this.deleteFinding();break;
        }
      },
      processDataImages(){
        this.slides = [];
        this.finding.Dataset.forEach(d=>{
          var path = 'https://kiwrious.org/Storage/'+ d.Media + '.jpg'
          this.slides.push(path);
        });
        console.log(this.slides);
      },
      switchTabs(index){
        this.selectedTab=index;
        switch(index){
          case 1 : this.inquiryList = this.replications; break;
          case 2 : this.inquiryList = this.variations; break;
        }
      }
    },
    watch: {
      interceptors(){
        if(this.interceptors>0 && !this.showLoading){
         this.$loading.show({delay:0, background: '#FFFFFF'});  
         this.showLoading = true;
        } else {
          this.showLoading = false;
          this.$loading.hide();
        }
      }
    }
  }
</script>

