  <v-container fluid>
    
    <v-container class="my-5">
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 @click="jump('/find_ar')">
          <v-card color="#FFECE4" class="rounded-lg text-left ma-3" style="text-align: left !important;" link>
            <v-img :src="require('@/assets/newInq.png')" aspect-ratio="1.4"
              lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg"
            ></v-img>
            <v-chip style="visibility: hidden;"
              class="ma-1 mb-0"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left>
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
                Username
              </div>
            </v-chip>
            <v-card-title class="py-1 mb-1 mt-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap; visibility: hidden;">Create new inquiry</v-card-title>
          </v-card>
        </v-flex>
        <v-flex xs12 sm6 md4 lg3 v-for="finding in myFindings" v-bind:key="finding.Id" @click="toFinding(finding)">
          <v-card class="rounded-lg text-left ma-3" style="text-align: left !important;" link>
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(finding)" aspect-ratio="1.4"
              lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg"
            >
              <v-avatar left v-if="finding.Type==4">
                <v-img :src="require('@/assets/flash-on-24-px@2x.png')"></v-img>
              </v-avatar>
              <v-chip v-if="finding.Status==1">
                Draft
              </v-chip>
            </v-img>
            <v-chip
              class="ma-1 mb-0"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left>
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
              {{finding.UserNickname}}
              </div>
            </v-chip>
            <v-card-title class="py-1 mb-1 mt-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{finding.Name}}</v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>

</template>

<script>
  import axios from 'axios'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL
  })
  var finding_list = {}
  export default {
    data() {
      return {
        myFindings:[]
      }
    },
    created(){
      this.$loading.show({delay:0, background: '#FFFFFF'});  
      const userId = sessionStorage.uid;
      api.get('/finding/user/'+userId).then((response)=> {
        this.$loading.hide();
        this.myFindings = response.data;
      });
    },
    beforeRouteEnter(to, from, next) {
      const uid = sessionStorage.uid
      const url = '/finding/user/' + uid
      api.get(url)
      .then((response) => {
        console.log(response)
        next()
      })
      .catch((error) => {
        console.log(error)
      })
    },
    methods: {
      jump(url) {
        this.$router.push({path: url})
      },
      getMedia(finding){
        if(finding.Dataset.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset[0].Media == null || finding.Dataset[0].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[0].Media+'.jpg';
      },
      toFinding(finding) {
        if(finding.Status == 1){
          // alert("navigating to inquiry editor");
          this.$store.dispatch('setFindingData', finding);
          this.$router.push({path: '/find_ar'});
          return;
        }
        if(finding.Type == 4){
          this.$store.dispatch('setFindingIdData', finding.Id)
          this.$router.push({path: '/view_challenge'})
        } else {
        this.$store.dispatch('setFindingIdData', finding.Id)
        this.$router.push({path: '/finding'})
        }
       
      },
    }
  }
</script>

<style scoped lang="scss">
  #inquiry_header {
    text-align: left;
    margin-left: 20px;
    font-size: 26px;
  }
  #inquiry_main {
    text-align: left;
  }
  .create {
    min-width: 300px;
    min-height: 300px;
    width: 20%;
    display: inline-block;
    // line-height: 160px;
    text-align: center;
    border: 1px;
    border-radius: 10px;
    background-color: #2F2F32;
    color: #FCFCFC;
    margin-left: 16px;
    cursor: pointer;
    div {
      padding: 80px;
      font-size: 36px;
      span {
        font-size: 70px;
      }
    }
  }
  .create:hover{
    background-color: #6F7071;
  }
  .draft-button{
    border-radius: 50px;
    width: 100px;
    font-size: 18px;
    color: white;
    padding: 5px;
    background-color: #2E2F32;
  }
</style>