// src/plugins/vuetify.js

import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify)

const opts = {
    theme: {
        themes: {
          light: {
            primary: '#FF623B',
            secondary: '#005665',
            accent: '#8c9eff',
            error: '#b71c1c',
            background: '#E3F6F5',
            background2: '#BAEAE8',
            primaryBackground: '#FFEBE3'
          },
        },
      },
}

export default new Vuetify(opts)