<template>
    <v-container fluid>
    <v-row no-gutters>
      <v-col justify="end">
        <v-text-field class="ml-2 mb-3 rounded-pill" style="width: 400px;"
          hide-details
          outlined
          clearable
          placeholder="Search inquiries"
          prepend-inner-icon="mdi-magnify"
          v-model="searchQuarry"
        ></v-text-field>
      </v-col>
      <v-col justify="end" class="text-right">
        <v-btn-toggle class="mr-3" style="height: 100%; vertical-align: top;">
          <v-btn color="primary" class="ml-3 rounded-pill" tile style="" @click="filterBySensor('')">
            All
          </v-btn>
          <v-btn v-for="sensorFilter in sensorFilters" :key="sensorFilter.key" 
            class="ml-3 rounded-pill" :active-class="sensorFilter.class" tile @click="filterBySensor(sensorFilter.key)">
            <v-avatar class="ma-3">
              <v-img
                :src="sensorFilter.icon"
              ></v-img>
            </v-avatar>
          </v-btn>
        </v-btn-toggle>
      </v-col>
    </v-row>   
    <v-container class="my-5" v-if="!searchQuarry">
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 v-for="challenge in challenges" v-bind:key="challenge.Id" @click="toFinding(challenge.Id)">
          <v-card class="rounded-lg text-left ma-3" style="text-align: left !important;" link>
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(challenge)" aspect-ratio="1.4"
              lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg"
            ></v-img>
            <v-chip
              @click.stop="searchQuarry=challenge.UserNickname"
              class="ma-1 mb-0"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left>
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
              {{challenge.UserNickname}}
              </div>
            </v-chip>
            <v-card-title class="mb-0 py-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{challenge.Name}}</v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container class="my-5" v-else>
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 v-for="challenge in filtered" v-bind:key="challenge.Id" @click="toFinding(challenge.Id)">
          <v-card class="rounded-lg text-xs-left ma-3" style="text-align: left !important;" link>
            <v-img :src="'https://kiwrious.org/Storage/'+ getMedia(challenge)" aspect-ratio="1.4"
            lazy-src="https://kiwrious.org/Storage/System/default-placeholder.jpg">
            </v-img>
            <v-chip
              class="ma-1 mb-0"
              color="#FFFFFF"
              text-color="black"
            >
              <v-avatar left>
                <v-icon>mdi-account-circle</v-icon>
              </v-avatar>
              <div>
              {{challenge.UserNickname}}
              </div>
            </v-chip>
            <v-card-title class="py-1 mb-1 mt-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{challenge.Name}}</v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>

</template>

<script>
import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL,
  })
export default {
    data(){
        return  {
            allchallenges: null,
            challenges: null,
            searchQuarry: "",
            filtered: null,
            sensorFilters:[
              {key:'uv', icon:require('@/assets/sensor-uv@2x.png'), class:'purple'},
              {key:'con', icon:require('@/assets/sensor-conductance@2x.png'), class:'yellow'},
              {key:'voc', icon:require('@/assets/sensor-voc@2x.png'), class:'green'},
              {key:'tem', icon:require('@/assets/sensor-humidity@2x.png'), class:'blue'},
              {key:'col', icon:require('@/assets/sensor-colour@2x.png'), class:'cyan'}
            ]
        }
    },
    created(){
        api.get('/challenge/').then((response)=> {
        // this.interceptors--;
        // this.$loading.hide();
        this.challenges = response.data;
        this.allchallenges = response.data;

        console.log(this.challenges);
        // if(this.finding.Base){
        //   api.get('/finding/'+this.finding.Base).then((response)=> {
        //     this.parentFinding = response.data;
        //     console.log(this.parentFinding);
        //   });
        // }
      }).catch((error)=>{
        // this.interceptors--; 
        alert(error.message);
      });
    },
    methods:{
        jump(url) {
            this.$router.push({path: url})
        },
        toFinding(fid) {
            debugger;
            this.$store.dispatch('setFindingIdData', fid)
            this.$router.push({path: '/view_challenge'})
        },
        getMedia(finding){
            if(finding.Dataset[0].Media == null){
            return "default-finding-image.jpg";
            }
            return finding.Dataset[0].Media+'.jpg';
        },
        filterResults(){
            this.filtered = this.challenges.filter(finding=>finding.Name.toLowerCase().includes(this.searchQuarry.toLowerCase()) || finding.UserNickname.toLowerCase().includes(this.searchQuarry.toLowerCase()));
        },
        filterBySensor(sensorName){
            if(!sensorName){
                this.challenges = this.allchallenges;
                return;
            }
            this.challenges = this.allchallenges.filter(c=>c.SensorId.includes(sensorName));
        }
    },
    watch: {
        searchQuarry(){
         this.filterResults();
       }
    }
}
</script>

<style scoped>
    .el-button {
        vertical-align: top;
        border: none;
        background-color: #F4F7FA !important;
        padding: 10px !important;
        height: 60px !important;
        width: 80px !important;
        font-size: 20px;
    }
    .el-button:hover {
        background-color: #FFEEE7  !important;
    }
    .el-button:focus {
        background-color: #FFEEE7 !important;
    }
    .purple{
      background-color: purple;
    }
    .yellow{
      background-color: yellow;
    }
    .green{
      background-color: green;
    }
    .blue{
      background-color: blue;
    }
    .cyan{
      background-color: cyan;
    }
</style>